.whises-container {
    flex-direction: column;
    align-items: center;    
    padding: 6vmin;
    background: 
        url('../../images/line-flower-1.png') right top / 30vw no-repeat,
        url('../../images/line-flower-2.png') left top / 30vw no-repeat,
        url('../../images/line-flower-3.png') left bottom / 30vw no-repeat,
        url('../../images/line-flower-4.png') right bottom / 30vw no-repeat;
    background-color: #F2EBEB;
    color: $gray-wed;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    .title {
        font-family: 'Pacifico', cursive;
        color: $green-wed;
        font-weight: 400;
        font-size: 7vmin;
        line-height: 8vmin;
        margin: 0;
        margin-bottom: 3vmin;
        text-align: center;
    }
    .deskripsi {
        width: 90vmin;        
        text-align: center;
        margin-bottom: 3vmin;
        font-size: 3vmin;
    }
    .message-history{
        max-height: 60vh;
        width: 85vw;
        overflow-y: scroll;

        .incoming-message{
            margin-top: 2vmin;
            margin-bottom: 2vmin;
            display: inline-block;
            vertical-align: top;
            width: 92%;

            .sender{
                font-size: 4vmin;
            }

            .message{
                background-color: white;
                padding: 2vmin;
                font-size: 4vmin;
                box-shadow: 0.5vmin 0.5vmin 1vmin #aaaaaa;
                margin-top: 1vmin;
                border-radius: 2vmin;
                margin-bottom: 1vmin;

                .time-date{
                    color: #747474;
                    opacity: 0.6;
                    display: block;
                    text-align: right;
                    font-size: 3vmin;
                    margin-top: 2vmin;
                }
            }
        }
    }

    .type-message{
        margin-top: 4vmin;
        width: 100%;
        .sender-name{
            width: 90%;
            padding: 2vmin;
            border-radius: 2vmin;
        }
        .message-text{
            width: 90%;
            padding: 2vmin;
            font-family: 'Josefin Sans', sans-serif;
            height: 20vmin;
            margin-top: 3vmin;
            border-radius: 2vmin;
            resize: none;
        }
        .send-button{
            background: #05728f none repeat scroll 0 0;
            border: medium none;
            color: #fff;
            cursor: pointer;
            font-size: 4vmin;
            border-radius: 2vmin;
            margin: 3vmin auto;
            display: flex;
            padding: 1.5vmin 6vw;
        }
    }
}