.penutup-container {
    display: flex;
    flex-direction: column;
    align-items: center;    
    padding: 6vmin;
    background: 
        url('../../images/thanks-1.png') center bottom / 40vw no-repeat;
    background-color: #FDF9F6;
    color: $gray-wed;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    .title {
        font-family: 'Pacifico', cursive;
        color: $green-wed;
        font-weight: 400;
        font-size: 7vmin;
        line-height: 8vmin;
        margin: 0;
        margin-bottom: 3vmin;
        text-align: center;
    }
    .deskripsi {
        width: 90vmin;        
        text-align: center;
        margin-bottom: 3vmin;
        font-size: 3vmin;
    }
    .salam {
        width: 90vmin;
        text-align: center;     
        font-size: 3vmin;   
    }
    .illustration {
        img {
            width: 6vmax;
        }
        margin-top: 4vw;
        margin-bottom: 1vw;
        text-align: center;
    }
    .thanks-img{
        img {
            width: 30vw;
        }
        margin-bottom: 4vw;
        text-align: center;
    }
}