.akad-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: 
        url('../../images/green-1.png') left bottom / 40vw no-repeat, 
        url('../../images/green-2.png') right bottom / 40vw no-repeat,
        url('../../images/green-3.png') left top / 40vw no-repeat,
        url('../../images/green-4.png') right top / 40vw no-repeat;
    color: white;
    padding: 5vh 0;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    .judul {
        margin-bottom: 1vmin;
        p {
            font-family: 'Pacifico', cursive;
            font-size: 5.3vmin;
            color: #6E8E7E;
            padding: 0 2vw;
            text-align: center;

        }
    }
    .text p {
        font-size: 2.4vmin;
        margin: 0;
        color: #6E8E7E;
        margin-bottom: 1vh;
    }
    .info {
        width: 70vmin;
        max-width: 60%;
        border: 2.5px dashed #6E8E7E;
        border-radius: 2vmin;
        margin: 0 1vw;
        margin-bottom: 3vmax;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 1.5vmax;
        &-judul {
            width: 100%;
            text-align: center;
            h2 {
                margin: 0;
                font-size: 4vmin;
                color: #6E8E7E;
                font-weight: 700;
            }
            div { 
                width: 70%;
                height: .1vw;
                margin: .8vmax auto 1.8vmax;
                background: white;
            }
        }
        .detail {
            &-wl {
                display: flex;
                align-items: center;
                p {
                    font-size: 3vmin;
                    color: #86AA9E;
                    margin-left: 3vw;
                }
                margin-bottom: 2vmin;
            }
            &-icon {
                width: 10vw;
            }
        }

        .btn {
            display: inline-flex;
            &-link {
                text-decoration: none;
                background-color: #6E8E7E;
                display: flex;
                align-items: center;
                padding: .5vmax 5vmin .5vmax 5vmin;
                border-radius: 2vmin;
                margin-left: 1vw;
                margin-right: 1vw;
                margin-top: 1vw;
                margin-bottom: 1vw;
                p {
                    color: white;
                    font-size: 3vmin;
                }
            }
            @media screen and (max-width: 800px) {
                flex-direction: column;
            }
        }
    }
}