.undangan-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;   
    min-height: 100vh;
    display: flex;
    justify-content: center;
    background: url('../../images/leaves-left.png') left center / 5vh no-repeat, url('../../images/leaves-right.png') right center / 5vh no-repeat;
    padding: 2vh 0;
    .pembuka {
        margin-top: 5vmin;
        margin-bottom: 2%;
        p {
            font-size: 13vmin;
            font-family: 'Almarai', sans-serif;
            font-weight: 400;
            margin: 0;
        }        
    }
    .frame {
        img {
            width: 50vw;
        }
        margin-bottom: 3%;
    }
    .countdown {
        display: flex;
        align-items: flex-start;
        margin-top: 5vmin;

        .time {
            text-align: center;
        }
        h1, p {
            margin: 0;
        }
        h1 {
            font-size: 9vmin;
            font-weight: 400;
        }
        p {
            font-weight: bold;
            font-size: 2.4vmin;
        }
        .divider {
            font-size: 5vw;
            margin: 0 .5vw;
        }
        margin-bottom: 3%;
    }
    .deskripsi {
        text-align: center;
        width: 60%;
        P {            
            font-size: 3vmin;
            
        }
    }
}