.header-container {
    background: 
        url('../../images/flower-1.png') left bottom / 20vw no-repeat, 
        url('../../images/flower-2.png') right bottom / 20vw no-repeat,
        url('../../images/flower-3.png') left top / 20vw no-repeat,
        url('../../images/flower-4.png') right top / 20vw no-repeat;
    background-color: rgba(33, 31, 47, 1);
    height: 100vh;    
    width: 100%;        
    .info {
        display: flex;
        color: white;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        height: 85%;        
        &-undangan {            
            img {
                width: 14vmin;
            }
            h1 {
                font-family: 'Pacifico', cursive;
                // font-size: 8vmin;
                font-size: 8vmin;
                line-height: 200%;
                margin: 0;
                font-weight: 300;            
            }
            h4 {
                color: #FFC700;
                // font-size: 5vmin;
                font-size: 5vmin;
                margin: 0;
                line-height: 200%;
                font-weight: 400;
            }
        }
        &-tamu {  
            margin-top: 3%;  
            margin-bottom: 3vmin; 
            width: 100%;                      
            p {
                margin: 0;
                &:first-child {
                    font-family: 'Pacifico', cursive;
                    // font-size: 3vmin;
                    font-size: 3vmin;
                }
                &:nth-child(2) {
                    // font-size: 4.2vmin;
                    font-size: 4.2vmin;
                    margin: 1vmin 0 2.5vmin;                              
                }
            } 
            div { 
                width: 60vmin;
                height: .25vw;
                margin: 0 auto;
                background: white;
            }
        }
        .btn-more {
            display: inline-flex;
            .btn-link {
                text-decoration: none;
                background-color: white;
                display: flex;
                align-items: center;
                padding: .5vmax 5vmin .5vmax 5vmin;
                border-radius: 2vmin;
                margin-left: 1vw;
                margin-right: 1vw;
                margin-top: 1vw;
                margin-bottom: 1vw;
                p {
                    color: rgba(33, 31, 47, 1);
                    font-size: 3vmin;
                }
            }
            @media screen and (max-width: 800px) {
                flex-direction: column;
            }
        }

        .hide {
            opacity: 0;
        }

        .show {
            transition: opacity 0.5s;
            opacity: 1;
        }
    }

    .bottom-ornament {
        width: 100%;
        height: 100%;
        z-index: 1;
        bottom: 0;
        background: url('../../images/flower-1.png') left bottom / 20vw no-repeat, url('../../images/flower-2.png') right bottom / 20vw no-repeat;
    }
}